@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
*{font-family:"Montserrat"}body{margin:0;padding:0;font-family:"Montserrat";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1{font-size:4rem;line-height:60px;font-weight:bold;margin:0}h2{margin:0;font-size:3rem;font-weight:bold}h3{margin:0;font-size:16px}

.About{display:flex;flex-direction:row;justify-content:center;animation:fadeInAnimation ease 3s;animation-iteration-count:1;animation-fill-mode:forwards}.About__content{display:flex;width:80vw;flex-direction:row;justify-content:space-between}.About__text{width:50%}.About__imageContainer{width:50%;height:100%;display:flex;justify-content:center;align-items:center;justify-content:center;overflow:hidden}.image{width:300px;height:400px}@keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}

.Header{width:100vw;margin-top:10vh;display:flex;flex-direction:column;align-items:center;animation:fadeInAnimation ease 3s;animation-iteration-count:1;animation-fill-mode:forwards}.Header__text{width:50vw;display:flex;flex-direction:column;align-items:center;align-content:center;text-align:center;grid-gap:1.5rem;gap:1.5rem}.Header__text button{text-decoration:none;padding:10px 30px;opacity:1;transition:all 0.2s ease-in-out;font-size:16px;background-color:black;color:white;outline:none;border:0px;border-radius:10px;cursor:pointer}.Header__text button:hover{opacity:0.6}.Header h2{font-size:16px}@keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}

.Skills{width:100vw;display:flex;flex-direction:column;align-items:center;grid-gap:4rem;gap:4rem}.Skills__container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;grid-column-gap:2px;-webkit-column-gap:2px;column-gap:2px;grid-row-gap:40px;row-gap:40px;width:80vw}.Skills__skillCard{width:30%;height:200px;display:flex;flex-direction:column;align-items:center;align-content:center;text-align:center;grid-gap:5px;gap:5px}.iconSkill{width:60px;height:60px}.icon{width:100%;height:100%}

.Works{width:100vw;display:flex;flex-direction:column;align-items:center;grid-gap:2rem;gap:2rem}.Works__container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;grid-gap:2rem;gap:2rem;width:80vw}.Works__Work{width:30%;height:200px;background-color:burlywood}

.Footer{margin:10vh 0 10vh 0;display:flex;flex-direction:column;align-items:center;grid-gap:20px;gap:20px}.Footer__iconContainer{display:flex;flex-direction:row;grid-gap:10px;gap:10px}.Footer__iconContainer__icon{width:2rem;height:2rem;cursor:pointer}.email-link{text-decoration:none;color:black;cursor:pointer}

.Nav{margin:auto;width:100vw;overflow:hidden;height:auto;display:flex;flex-direction:row;align-items:center;justify-content:space-around;margin-top:10vh;background-color:white;z-index:10;opacity:0.90}.Nav__icon{font-weight:bold}.Nav__LinksContainer{display:flex;flex-direction:row;grid-gap:2rem;gap:2rem;padding:1rem}.Nav__LinksContainer__Link{text-decoration:none;color:black;transition:all 0.5s ease-in-out;font-family:"Open Sans";opacity:0.6;font-size:16px;background-color:transparent;outline:none;border:0px;cursor:pointer}.Nav__LinksContainer__Link:hover{opacity:1}.sticky{position:-webkit-sticky;position:sticky;top:0}

main{display:flex;flex-direction:column;grid-gap:35vh;gap:35vh}

